.adm-title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 18px;
}

.adm-section {
  margin-top: 24px;
}

.adm-onboarding {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
}

@media (max-width: 1200px) {
  .adm-onboarding { grid-template-columns: 1fr; }
}
